import React, { Component } from "react";

class CreateAccountSearch extends Component {
  render() {
    return (
      <div>
        <section className="dia3 dia2 dia4">
          <div
            className="modal fade"
            id="DIA4-Search"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body ">
                  <div className="dia3-content text-center">
                    <p>Search Team</p>
                  </div>
                  <div className="dia3-button">
                    <form action="">
                      <div className="form-row">
                        <div className="form-group col-md-12">
                          <label for="">Team Name</label>
                          <input
                            type="text"
                            className="form-control"
                            id=""
                            placeholder=""
                          />
                        </div>
                        <div className="form-group col-md-12">
                          <label for="">City</label>
                          <input
                            type="text"
                            className="form-control"
                            id=""
                            placeholder=""
                          />
                        </div>
                        <div className="form-group col-md-12">
                          <label for="">State</label>
                          <input
                            type="text"
                            className="form-control"
                            id=""
                            placeholder=""
                          />
                        </div>
                      </div>
                      <div className="dia2-save-buttomn">
                        <a href="#">Search</a>
                      </div>
                    </form>
                  </div>
                  <div className="container mt-50">
                    <div className="row">
                      <div className="col-xl-12">
                        <div className="dia-4-search-title text-center">
                          <p>Team Search</p>
                        </div>
                        <div className="dia-4-search-table">
                          <div className="table-responsive">
                            <table className="table">
                              <thead>
                                <tr>
                                  <th scope="col">Team Name</th>
                                  <th scope="col">City</th>
                                  <th scope="col">State</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                  <th scope="row"></th>
                                  <td></td>
                                  <td></td>
                                </tr>
                                <tr>
                                  <th scope="row"></th>
                                  <td></td>
                                  <td></td>
                                </tr>
                                <tr>
                                  <th scope="row"></th>
                                  <td></td>
                                  <td></td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-xl-6">
                        <div className="dia-4-search-button">
                          <a href="">Cancel</a>
                        </div>
                      </div>
                      <div className="col-xl-6 text-right">
                        <div className="dia-4-search-button ">
                          <a href="">Select Team</a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        ;
      </div>
    );
  }
}

export default CreateAccountSearch;
