import React, { Component } from 'react';
import CreateAccountHeader from '../components/CreateAccountComponents/CreateAccountHeader';
import CreateAccountForms from '../components/CreateAccountComponents/CreateAccountForms';
import LoginHeader from '../components/LoginComponents/LoginHeader';


class CreateAccount extends Component {
    render() {
        return (
            <div>
                <LoginHeader headerMenu='item-hide'/>
                <CreateAccountHeader/>
                <CreateAccountForms/>
            </div>
        )
    }
}

export default CreateAccount;
