import React, { Component } from "react";
import { Link } from 'react-router-dom';

class LoginButtons extends Component {
  render() {
    return (
      <div>
        <div class="row">
          <div className="col-xl-12">
            <div className="signin-button-area">
              <Link to='/admin-login'>Sign In As Coach/Admin</Link>
              <Link to='/team-member-login'>Sign in as Team Member</Link>
              <Link to='/director-login'>Sign in as Director</Link>
              <Link to='/event-worker-login'>SIGN in as event worker</Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LoginButtons;
