import React, { Component } from 'react'
import LoginHeader from '../components/LoginComponents/LoginHeader'
import DirectorLoginForm from '../components/LoginComponents/DirectorLoginForm'



class DirectorLogin extends Component {
    render() {
        return (
            <div>
                <LoginHeader headerMenu='item-hide' />
                <section class="main pt-100">
                    <div class="d-flex align-items-center">
                        <div class="container">
                            <div class="row">
                                <div class="col-xl-12">
                                    <div class="hero-logo-area text-center">
                                        <img src={require( '../assets/images/takedoenlogo.png')} alt="takedown-logo" />
                                    </div>
                                </div>
                                <div class="col-xl-12">
                                    <div class="hero-content text-center">
                                    <p>Sign in as a Director to create events such as <br/> tournaments, dual &amp; tri meets. Create brackets and <br/> print them off to display to wrestlers</p>
                                    </div>
                                </div>
                            </div>
                            <DirectorLoginForm/>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default DirectorLogin;