import React, { Component } from "react";
import LoginButtons from "./LoginButtons";

class LoginBanner extends Component {
  render() {
    return (
      <div>
        <section className="login-main">
          <div classNameName="d-flex align-items-center">
            <div className="container">
              <div className="row">
                <div className="col-xl-12">
                  <div className="hero-logo-area text-center">
                    <img src={require("../../assets/images/takedoenlogo.png")} alt="takedown-logo" />
                  </div>
                </div>
                <div className="col-xl-12">
                  <div className="hero-content text-center">
                    <p>
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                      sed do <br /> eiusmod tempor incididunt ut labore et
                      dolore.
                    </p>
                  </div>
                  <div className="hero-button-area text-center">
                    <button className="main-btn">
                      Sign up today for an account
                    </button>
                  </div>
                </div>
              </div>
              <LoginButtons />
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default LoginBanner;
