import React, { Component } from 'react'
import LoginHeader from '../components/LoginComponents/LoginHeader'
import EventWorkerLoginForm from '../components/LoginComponents/EventWorkerLoginForm'




class EventWorkerLogin extends Component {
    render() {
        return (
            <div>
                <LoginHeader headerMenu='item-hide' />
                <section class="main pt-100">
                    <div class="d-flex align-items-center">
                        <div class="container">
                            <div class="row">
                                <div class="col-xl-12">
                                    <div class="hero-logo-area text-center">
                                        <img src={require( '../assets/images/takedoenlogo.png')} alt="takedown-logo" />
                                    </div>
                                </div>
                                <div class="col-xl-12">
                                    <div class="hero-content text-center">
                                    <p>Event workers can login to run match tables.</p>
                                    </div>
                                </div>
                            </div>
                            <EventWorkerLoginForm/>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default EventWorkerLogin;