import React, { Component } from 'react';

class CreateAccountTeamInfo extends Component {
    render() {
        return (
            <div>
                <div className="frm16-team-info">
                        <p>Team Info</p>
                        <ul>
                            <li>Team Name : Tigers Youth</li>
                            <li>Coaches : John Doe, Harry Lemon</li>
                            <li>City : Hillard</li>
                            <li>State : Ohio</li>
                        </ul>
                    </div>
                    <div className="create-an-account-button-one text-center mt-50">
                        <a href="/#" data-toggle="modal" data-target="#exampleModal">Add Team</a>
                    </div>
                    
            </div>
        )
    }
}

export default CreateAccountTeamInfo;
