import React, { Component } from "react";

class TeamMemberLoginForm extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-xl-12">
            <div className="form-area-header text-center">
              <h3>team member sign-in</h3>
            </div>
            <div className="form-area text-center">
              <form action="">
                <input
                  className="form-control"
                  type="text"
                  placeholder="Team Name"/>
                <input
                  className="form-control" type="password" placeholder="Password" />
              </form>
              <div className="form-button-area">
                <button>Sign-in</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TeamMemberLoginForm;
