import React, { Component } from 'react'
import LoginHeader from '../components/LoginComponents/LoginHeader'
import TeamMemberLoginForm from '../components/LoginComponents/TeamMemberLoginForm'


class TeamMemberLogin extends Component {
    render() {
        return (
            <div>
                <LoginHeader headerMenu='item-hide' />
                <section class="main pt-100">
                    <div class="d-flex align-items-center">
                        <div class="container">
                            <div class="row">
                                <div class="col-xl-12">
                                    <div class="hero-logo-area text-center">
                                        <img src={require( '../assets/images/takedoenlogo.png')} alt="takedown-logo" />
                                    </div>
                                </div>
                                <div class="col-xl-12">
                                    <div class="hero-content text-center">
                                        <p> Sign in to your team to view wrestlers on the team, <br/> coaches, wrestlers stats, and upcoming events.</p>
                                    </div>
                                </div>
                            </div>
                            <TeamMemberLoginForm/>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default TeamMemberLogin;