import React, { Component } from 'react'

class DirectorLoginForm extends Component {
    render() {
        return (
            <div>
                  <div className="row">
                    <div className="col-xl-12">
                        <div className="form-area-header text-center">
                            <h3>Director login</h3>
                        </div>
                        <div className="form-area text-center">
                            <form action="">
                                <input className="form-control" type="text" placeholder="Username"/>
                                <input className="form-control" type="text" placeholder="Director ID"/>
                                <input className="form-control" type="password" placeholder="Password"/>
                            </form>
                            <div className="form-button-area">
                                <button>Sign-in</button>
                            </div>
                        </div>
                    </div> 
                </div>
            </div>
        )
    }
}

export default DirectorLoginForm;
